// 
/**
 * @param value value,,,value
 * **/
export function error(value) {
	if(value === '') {
		uni.showToast({
			title: ',',
			icon: 'none'
		})
	} else if(value === '') {
		uni.showToast({
			title: '',
			icon: 'none'
		})
	} else {
		uni.showToast({
			title: value,
			icon: 'none'
		})
	}
}