/**
 * 
 * @param name 
 * @param rule , 
 * @param value 
 * **/
 // 
export function inputCheck(name, rule, value) {
	// 
	if (value.length == 0 || value == null || value == '') {
		return name + '';
	} else {
		// 
		switch (rule) {
			case 'phone':
			// 1--12--3,4,5,7,8,3--0-99
			var reg = /^[1][3,4,5,7,8][0-9]{9}$/;
			if(!reg.test(value)){
				return ''
			}
			break;
			case 'password':
			var reg = /^(?![0-9]+$)(?![a-z]+$)(?![A-Z]+$)(?!([^(0-9a-zA-Z)])+$).{6,14}$/;
			if(!reg.test(value)) {
				return '614'
			}
			break;
			case 'identify':
			var reg = /^[1-9]\d{5}(18|19|20)\d{2}((0[1-9])|(1[0-2]))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/;
			if(!reg.test(value)){
				return ''
			}
			break;
			case "string":
			return "ok"
			break;
			case 'identifyCode':
			var reg = /^[0-9]{6}$/;
			if(!reg.test(value)) {
				return '6'
			}
			break;
		}
		return "ok"
	}
}
// 
export function checkPassword(password, checkPassword){
	if(checkPassword.length ==0 || checkPassword == null || checkPassword == '') {
		return ''
	} else {
		if(password !== checkPassword) {
			return ''
		} else {
			return 'ok'
		}
	}
}

// YYYY-MM-DD
export function changeDateByIdentify(date){
	var newDate = ''
	if(date.length == 8){
		newDate = date.slice(0,4) + '-' + date.slice(4,6) + '-' + date.slice(6,8)
		return newDate 
	} else {
		return 'error'
	}
}

//   1-  2-
export function checkGender(identify){
	var num = identify.slice(16,17)
	if(num % 2 === 0) {
		// 
		return 2
	} else {
		return 1
	}
}