/**
 * YYYY-MM-DD hh:mm:ss
 * @param date 
 * @param rule  YYYY-MM-DD YYYY-MM-DD hh:mm:ss day(), date()
 *              week-date()
 **/

// getDay()...
function getDay(day) {
	switch (day) {
		case 0:
			return '';
			break;
		case 1:
			return '';
			break;
		case 2:
			return '';
			break;
		case 3:
			return '';
			break;
		case 4:
			return '';
			break;
		case 5:
			return '';
			break;
		case 6:
			return '';
			break;
	}
}

// 0
function turnDouble(num) {
	// 2
	if (num >= 10) {
		return num
	} else { // 0
		return parseInt('0' + num)
	}
}

// 
function getWeekDate() {
	let currentDay = new Date()
	let dateList = []
	// 
	dateList.push({
		date: turnDouble(currentDay.getDate()),
		day: getDay(currentDay.getDay())
	})
	// 6
	for(let i=0;i<6;i++) {
		currentDay.setDate(currentDay.getDate() + 1)
		 dateList.push({
		date: turnDouble(currentDay.getDate()),
		day: getDay(currentDay.getDay())
	})
	}
	return dateList
}

export function formDate(date, rule) {
	let year = date.getFullYear() // 
	let month = date.getMonth() + 1 // 
	let today = date.getDate() // 
	let day = date.getDay() // 
	let hour = date.getHours() // hour
	let minute = date.getMinutes() // 
	let second = date.getSeconds() // 
	switch (rule) {
		case 'YYYY-MM-DD':
			return year + '-' + turnDouble(month) + '-' + turnDouble(today);
			break;
		case 'YYYY-MM-DD hh:mm:ss':
			return year + '-' + turnDouble(month) + '-' + turnDouble(today) +
				' ' + turnDouble(hour) + ':' + turnDouble(minute) + ':' + turnDouble(second);
			break;
		case 'day':
			return getDay(day);
			break;
		case 'date':
			return turnDouble(today);
			break;
			case 'week-date': 
			return getWeekDate();
			break;
	}
}
